$big-title__color: $color--blue;
$big-title__color--blue: transparentize(white, 0.1);


body.is-blue {
	color: $big-title__color--blue;

	.big-title, .perex {
		color: $big-title__color--blue;
	}

	.small-title {
		color: #6079E7;
	}
}

p {
	margin-bottom: 25px;
	a {
		font-family: "Pepi/Rudi Medium";
		font-weight: normal;
		font-style: normal;
		text-decoration: underline;
		color: black;
		position: relative;
		padding-right: 5px;
		&:after {
			content: '↗';
			font-family: "Pepi/Rudi Regular";
			font-size: 1.3rem;
			position: absolute;
			right: -2px;
			top: -4px;
			line-height: 1;
			transition: transform 0.2s ease-in-out;
		}

		&:hover {
			&:after {
				transform: translate(2px, -2px);
			}
		}
	}
}

.big-title {
	font-size: 7.2rem;
	line-height: 8.8rem;
	color: $big-title__color;
	@media screen and (min-width: $viewport--xxl) {
		line-height: 9.2rem;
	}
}

.perex {
	margin-bottom: 30px;
	font-size: 2rem;
	line-height: 3.6rem;
	color: #0E1122;
}

.small-title {
	color: $color--grey-darker;
	text-transform: uppercase;
	font-family: "Pepi/Rudi Medium";
	font-weight: normal;
	font-style: normal;
	font-size: 1.2rem;
	letter-spacing: 2px;
}